/* Button.css (Optional styling) */
.custom-button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .custom-button:hover {
    background-color: #0056b3;
  }
  
  .custom-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }